@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@400;700&display=swap);
.movieRow{
    margin-bottom: 30px;
}

.movieRow h2{
    margin: 0px 0px 0px 30px;
}

.movieRow--listarea{
    overflow-x: hidden;
    padding-left: 30px;
}

.movieRow--list{
    transition: all ease 0.5s;
}

.movieRow--item{
    display: inline-block;
    width: 210px;
    cursor: pointer;
}

.movieRow--item img{
    width: 100%;
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
    transition: all ease 0.3s;
}

.movieRow--item img:hover{
    -webkit-transform: scale(1);
            transform: scale(1);
}

.movieRow--left,
.movieRow--right{
    position: absolute;
    width: 40px;
    height: 315px;
    background-color: rgba(0,0,0,0.6);
    z-index: 99;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    overflow: hidden;
    cursor: pointer;
    opacity: 0;
    transition: all ease 0.5s;
}

.movieRow--left{
    left: 0;
}

.movieRow--right{
    right: 0;
}

.movieRow:hover .movieRow--left,
.movieRow:hover .movieRow--right{
    opacity: 1;
}

@media(max-width:770px){
    .movieRow--left,
    .movieRow--right{
        opacity: 1;
    }
}
*{
    box-sizing: border-box;
}

body{
    background-color: #111;
    color: #fff;
    margin: 0;
    font-family: 'Roboto', sans-serif;
}

.lists{
    margin-top: -130px;
}

footer{
    margin: 50px 0;
    text-align: center;
}
.featured{
    height: 100vh;
}

.featured--vertical{
    width: inherit;
    height: inherit;
    background: linear-gradient(to top, #111 10%, transparent 90%);
}

.featured--horizontal{
    width: inherit;
    height: inherit;
    background: linear-gradient(to right, #111 30%, transparent 70%);
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    padding-left: 30px;
    padding-bottom: 130px;
    padding-top: 70px;
}

.featured--name{
    font-size: 60px;
    font-weight: bold;
}

.featured--info{
    font-size: 18px;
    margin-top: 15px;
}

.featured--points,
.featured--yaer,
.featured--seasons{
    display: inline-block;
    margin-right: 15px;
}

.featured--points{
    color: #46d369;
}

.featured--points span{
    color: white !important;
}

.featured--description{
    margin-top: 15px;
    font-size: 20px;
    color: #999;
    max-width: 40%;
}

.featured--buttons{
    margin-top: 15px;
}

.featured--infobutton{
    display: inline-block;
    font-size: 20px;
    padding: 12px 20px;
    font-weight: bold;
    border-radius: 5px;
    text-decoration: none;
    margin-right: 10px;
    background-color: #333;
    color: #FFF;
    opacity: 1;
    transition: all ease 0.4s;
}

.featured--infobutton:hover{
    opacity: 0.8;
}

.featured--genres{
    margin-top: 15px;
    font-size: 18px;
    color: #999;
}

@media (max-width:770px) {
    .featured{
        height: 90vh;
    }

    .featured--name{
        font-size: 40px;
    }

    .featured--info{
        font-size: 16px;
    }

    .featured--description{
        font-size: 14px;
        max-width: 100%;
        margin-right: 40px;
    }

    .featured--buttons{
        font-size: 16px;
    }

    .featured--genres{
        font-size: 14px;
    }

}
header{
    position: fixed;
    z-index: 999;
    top: 0;
    left: 0;
    right: 0;
    height: 70px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    padding: 0 30px;
    background: transparent;
    transition: all ease 0.5s;
}

header.black{
    background-color: #141414;
}

.header--logo{
    height: 50px;
}

.header--logo img{
    height: 100%;
    cursor: pointer;
}

.header--search{
    height: 30px;
}

.header--search img{
    height: 100%;
}
html {
  font-size: 10px;
  font-family: 'Nunito', sans-serif;
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  background-color: #141414;
  color: white;
}

.form{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.label{
  font-size: 1.2rem;
  margin-bottom: 0.2rem;
  text-transform: uppercase;
}

.input{
  margin-top: 150px;
  width: 400px;
  font-size: 1.6rem;
  padding: 0.5rem 2rem;
  line-height: 2.8rem;
  border-radius: 15px;
  border: 1px solid #444444;
  margin-bottom: 1rem;
}


.button{
  height: 42px;
  width: 150px;
  margin-top: 2rem;
  margin-bottom: 25px;
  background-color: #DD342F;
  color: white;
  padding: 1rem 2rem;
  border: 1px solid rgba(0, 0, 0, 0.75);
  border-radius: 15px;
  font-size: 2rem;
  cursor: pointer;
  transition: 0.3s;
}

.button:hover{
  opacity: 0.7;
}

@media (min-width: 786px){
  .form{
    grid-template-columns: auto 1fr auto;
    grid-gap: 1rem;
    -webkit-align-items: center;
            align-items: center;
  }

  .input{
    margin-bottom: 0;
  }
}

.card--list{
  margin-top: 4rem;
  display: inline-block;
}

.img--poster{
    display: inline-block;
    width: 200px;
    cursor: pointer;
}

.img--poster img{
    width: 100%;
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
    transition: all ease 0.3s;
}

.img--poster img:hover{
    -webkit-transform: scale(1);
            transform: scale(1);
}
